<template>
  <div>
    <el-carousel @change="carouselChange" :interval=interval arrow="always" height="100%" class="carousel-pane" indicator-position="inside">
      <el-carousel-item class="carousel-pane-item">
        <div class="fullscreen_post_bg cat-1"></div>
        <el-row type="flex" justify="end">
          <el-col :lg="12" :md="12" :sm="12" :xs="24">
            <div class="text-pane">
              <transition name="title">
                <p v-if="show1" class="title">张翊卓</p>
              </transition>
              <transition name="title">
                <p v-if="show2" class="content">this is content</p>
              </transition>
            </div>
          </el-col>
        </el-row>
      </el-carousel-item>
      <el-carousel-item class="carousel-pane-item">
        <div class="fullscreen_post_bg cat"></div>
        <el-row>
          <el-col :span="6" :offset="12">
            <p style="color: white">YIZHUO</p>
          </el-col>
        </el-row>
      </el-carousel-item>
      <!-- <el-carousel-item class="carousel-pane-item">
        <div class="fullscreen_post_bg cat-moon"></div>
        <el-row>
          <el-col :span="6" :offset="12">
            <p style="color: white">YIZHUO</p>
          </el-col>
        </el-row>
      </el-carousel-item>
      <el-carousel-item class="carousel-pane-item">
        <div class="fullscreen_post_bg moon"></div>
        <el-row>
          <el-col :span="6" :offset="12">
            <p style="color: white">ZHANG</p>
          </el-col>
        </el-row>
      </el-carousel-item> -->
    </el-carousel>
  </div>
</template>

<script>
export default {
  name: 'Main',
  mounted () {
  },
  data () {
    return {
      msg: 'Welcome to My World !',
      interval: 7000,
      show1: false,
      show2: false
    }
  },
  methods: {
    carouselChange (key1, key2) {
      console.log(key1 + ' ' + key2)
      if (key1 === 0) {
        this.show1 = !this.show1
        this.show2 = !this.show2
      }
      if (key2 === 0) {
        this.show1 = !this.show1
        this.show2 = !this.show2
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import '../assets/css/main.css';
</style>



